import { useNavigate, useSearchParams } from "react-router-dom";
import "./browse.css";
import { useEffect, useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { PosterRenderer, SKINNY_RENDER } from "../component/poster";
import _ from "lodash";
import { ChunkDisplay } from "../component/chunked_display/display";

export const Browse = () => {
  const [query, setQuery] = useSearchParams();
  const [items, setItems] = useState<SKINNY_RENDER[]>([]);
  const nav = useNavigate();
  useEffect(() => {
    var url = "";
    var action = query.get("action");
    if (!action) {
      nav("/");
      return;
    }
    switch (action) {
      case "genre":
        var genre = query.get("genre");
        if (!genre) return nav("/");
        url = `http://localhost:8080/browse?genre=${genre}`;
        break;
      case "watchlist":
        url = `http://localhost:8080/watchlist`;
        break;
      case "movie":
        url = `http://localhost:8080/browse?type=movie`;
        break;
      case "tv":
        url = `http://localhost:8080/browse?type=tv`;
        break;
      default:
        nav("/");
        return;
    }
    fetch(url, { credentials : 'include'})
      .then((res) => res.json())
      .then((res) => setItems(res.elements));
  }, []);
  return <ChunkDisplay items={items} chunk_size={5} />;
};
