import React from "react";
import "./index.css";
import exp from "constants";
import { useNavigate } from "react-router-dom";
// id = po

// type SKINNY_RENDER struct {
// 	ID          int
// 	PROVIDER    string
// 	TYPE        string
// 	NAME        string
// 	POSTER      string
// 	BACKDROP    string
// 	DESCRIPTION string
// 	YEAR        string
// 	RUNTIME     int
// }
export interface GENRE {
  ID: number;
  NAME: string;
}
export interface SKINNY_RENDER {
  ID: number;
  TYPE: string;
  NAME: string;
  POSTER: string;
  BACKDROP: string;
  DESCRIPTION: string;
  YEAR: string;
  RUNTIME: number;
  GENRE: GENRE[];
}
export interface PosterRendererProps {
  render: SKINNY_RENDER;
  nav: any;
}
export const PosterRenderer = (props: SKINNY_RENDER) => {
  const nav = useNavigate();
  return <Porenderer render={props} nav={nav} />;
};
export class Porenderer extends React.Component<PosterRendererProps> {
  public ic = React.createRef<HTMLDivElement>();
  constructor(props: PosterRendererProps) {
    super(props);
  }
  onhoverImg = (e: React.MouseEvent<HTMLImageElement, MouseEvent>) => {
    this.ic.current?.style.setProperty("display", "block");
  };
  onunhoverImg = (e: React.MouseEvent<HTMLImageElement, MouseEvent>) => {
    this.ic.current?.style.setProperty("display", "none");
  };
  go() {
    console.log("go");
    this.props.nav(`/render/${this.props.render.TYPE}/${this.props.render.ID}`);
  }

  render() {
    return (
      <div
        className="po-ct"
        onClick={this.go.bind(this)}
        onMouseOver={this.onhoverImg.bind(this)}
        onMouseOut={this.onunhoverImg.bind(this)}
      >
        <img
          loading="lazy"
          className="po-img"
          src={this.props.render.POSTER}
          alt={this.props.render.NAME}
        />
        <div ref={this.ic} className="po-info">
          <div className="po-info-name">{this.props.render.NAME}</div>
          <div className="po-info-desc">{this.props.render.DESCRIPTION}</div>
        </div>
      </div>
    );
  }
}
