import { useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";
import { PosterRenderer, SKINNY_RENDER } from "../component/poster";
import "./search.css";
import _ from "lodash";
import { ChunkDisplay } from "../component/chunked_display/display";

export const SearchRender = (props: {}) => {
  const [params, setParams] = useSearchParams();
  const [elements, setElements] = useState([]);
  const querytext = params.get("q");

  useEffect(() => {
    const timeout = setTimeout(() => {
      fetch(`http://localhost:8080/search?query=${querytext}`, {
        credentials: "include",
      })
        .then((res) => res.json())
        .then((res) => setElements(res.elements));
    }, 100);
    return () => {
      console.log("clearing timeout");
      clearTimeout(timeout);
    };
  }, [params]);

  const query = params.get("q");
  if (querytext === null) {
    return <div>Search for something</div>;
  }
  return (
    <div>
      <h1>Search for: {query}</h1>
      <ChunkDisplay items={elements} chunk_size={5} />
    </div>
  );
};
