import React from "react";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { GENRE } from "../component/poster";
import exp from "constants";
import { app_url } from "..";
import "./render.css";

export const Render = (props: {}) => {
  const params = useParams();
  const navigate = useNavigate();
  return <Renderer {...{ params, navigate }} />;
};
export interface RendereProps {
  params: any;
  navigate: any;
}
export interface MovieItem {
  ID: string;
  TMDB_ID: number;
  DISPLAY_NAME: string;
  YEAR: string;
  FILES: FileItem[];
  PROVIDERS: PROVIDER[];
  DESCRIPTION: string;
  RUNTIME: number;
  VOTE: number;
  GENRE: GENRE[];
  BACKDROP: string;
  POSTER: string;
  DOWNLOAD_URL: string;
  TRANSCODE_URL: string;
}
export interface TVItem {
  ID: string;
  TMDB_ID: number;
  DISPLAY_NAME: string;
  YEAR: string;
  FILES: FileItem[];
  PROVIDERS: PROVIDER[];
  DESCRIPTION: string;
  RUNTIME: number;
  VOTE: number;
  GENRE: GENRE[];
  BACKDROP: string;
  POSTER: string;
  SEASONS: SEASON[];
}
export interface SEASON {
  UUID: string;
  SEASON_NUMBER: number;
  NAME: string;
  DESCRIPTION: string;
  BACKDROP: string;
  EPISODES: EPISODE[];
}
export interface EPISODE {
  UUID: string;
  EPISODE_NUMBER: number;
  FILES: FileItem[];
  NAME: string;
  DESCRIPTION: string;
  STILL: string;
  TRANSCODE_URL: string;
  DOWNLOAD_URL: string;
}
export interface FileItem {
  UUID: string;
  FILENAME: string;
  DOWNLOAD_URL: string;
  TRANSCODE_URL: string;
}
export interface PROVIDER {
  PROVIDER_ID: number;
  URl: string;
  PROVIDER_NAME: string;
  DISPLAY_PRIORITY: number;
}

export interface RenderState {
  item: MovieItem | TVItem;
  season: number;
}
class Renderer extends React.Component<RendereProps> {
  public id: string;
  public provider: string;
  public Type: string;
  public currentFile: FileItem = {} as FileItem;
  public state: RenderState = {
    item: {} as MovieItem | TVItem,
    season: 0,
    // currentFile: {} as FileItem,
  };
  constructor(props: RendereProps) {
    super(props);
    this.provider = props.params.provider;
    this.id = props.params.id;
    this.Type = props.params.Type;
  }
  componentDidMount(): void {
    document.body.style.overflow = "hidden";
    fetch(`${app_url}/render?id=${this.id}&type=${this.Type}`, {
      credentials: "include",
    }).then((response) => {
      response.json().then((data) => {
        this.setState({ item: data.item });
        console.log(data.item);
        if (data.item.FILES.length > 0) {
          this.currentFile = data.item.FILES[0];
        }
      });
    });
  }
  componentWillUnmount(): void {
    document.body.style.overflow = "auto";
  }
  download() {
    if (this.state.item.FILES.length > 0) {
      window.location.href = app_url + this.currentFile.DOWNLOAD_URL;
    } else {
      //@ts-ignore
      window.location.href = app_url + this.state.item.DOWNLOAD_URL;
    }
  }
  changeFile(e: React.ChangeEvent<HTMLSelectElement>) {
    console.log("cc");
    this.currentFile = this.state.item.FILES[parseInt(e.target.value)];
    console.log(this.currentFile.FILENAME);
  }
  stream() {
    console.log(
      typeof this.currentFile.TRANSCODE_URL,
      //@ts-ignore
      this.state.item.TRANSCODE_URL
    );
    if (typeof this.currentFile.TRANSCODE_URL != "undefined") {
      console.log(this.currentFile);
      var encoded = encodeURIComponent(this.currentFile.TRANSCODE_URL);
      this.props.navigate("/player/?transcode=" + encoded);
    } else {
      var item = this.state.item as MovieItem;
      window.location.href =
        "/player?transcode=" + encodeURIComponent(item.TRANSCODE_URL);
    }
  }
  browseGenre(id: number) {
    this.props.navigate(`/browse?action=genre&genre=${id}`);
  }
  epDl(i: number, e: number) {
    const item = this.state.item as TVItem;
    const season = item.SEASONS[i];
    const episode = season.EPISODES[e];
    window.location.href = app_url + episode.DOWNLOAD_URL;
  }
  epStr(episode: EPISODE) {
    console.log(episode);
    window.location.href =
      "/player?transcode=" + encodeURIComponent(episode.TRANSCODE_URL);
  }
  // id = rd
  public render() {
    // console.log(this.state.item);
    var serie = this.state.item as TVItem;
    var movie = this.state.item as MovieItem;
    if (typeof this.state.item.ID === "undefined") {
      return <div>Loading...</div>;
    }
    console.log(this.state.item);
    return (
      <div>
        <div className="rd-ctn">
          <img
            className="rd-img"
            src={this.state.item.POSTER}
            alt={this.state.item.DISPLAY_NAME}
          />
          <div className="rd-data">
            <div className="rd-name">{this.state.item.DISPLAY_NAME}</div>
            <div className="rd-desc">{this.state.item.DESCRIPTION}</div>
            {this.state.item.FILES.length > 0 ? (
              <>
                <select onChange={this.changeFile.bind(this)} className="rd-sl">
                  {this.state.item.FILES?.map((file, i) => {
                    return (
                      <option value={i} key={file.UUID}>
                        {file.FILENAME}
                      </option>
                    );
                  })}
                </select>
              </>
            ) : (
              <></>
            )}
            <br />{" "}
            <button className="rd-dl" onClick={this.download.bind(this)}>
              Download
            </button>
            <button onClick={this.stream.bind(this)} className="rd-str">
              Streaming
            </button>
            <div className="rd-genre">
              Genres :
              {this.state.item.GENRE?.map((genre) => {
                return (
                  <>
                    &nbsp;
                    <div
                      onClick={(e) => this.browseGenre(genre.ID)}
                      className="rd-genre-item"
                    >
                      {genre.NAME}
                    </div>
                    ,
                  </>
                );
              })}
            </div>
            <div className="rd-year">Sortie en {this.state.item.YEAR}</div>
            <div className="rd-runtime">
              Durée : {FormatRuntime(this.state.item.RUNTIME)}
            </div>
            <div className="rd-vote">
              Note : {Math.round(this.state.item.VOTE)}
            </div>
            {this.Type === "tv" ? (
              <div>
                <select
                  value={this.state.season}
                  onChange={(e) =>
                    this.setState({ season: parseInt(e.target.value) })
                  }
                  className="ep-sl"
                >
                  {serie.SEASONS?.map((season, i) => {
                    return (
                      <option value={i} key={season.UUID}>
                        {season.NAME} - {season.SEASON_NUMBER}
                      </option>
                    );
                  })}
                </select>
                <div className="ep-ctn">
                  {serie.SEASONS[this.state.season].EPISODES.map(
                    (episode, i) => {
                      return (
                        <div className="ep-item" key={episode.UUID}>
                          <img
                            className="ep-img"
                            src={
                              episode.STILL
                                ? episode.STILL
                                : this.state.item.POSTER
                            }
                            alt={episode.NAME}
                          />
                          <div className="ep-info">
                            <div className="ep-name">
                              {i} - {episode.EPISODE_NUMBER} - {episode.NAME}{" "}
                              <div
                                onClick={() => this.epDl(this.state.season, i)}
                              >
                                Download
                              </div>{" "}
                              <div onClick={(e) => this.epStr(episode)}>
                                Streaming
                              </div>
                            </div>
                            <div className="ep-desc">{episode.DESCRIPTION}</div>
                          </div>
                        </div>
                      );
                    }
                  )}
                </div>
              </div>
            ) : (
              <></>
            )}
          </div>
        </div>
        <div
          style={{
            backgroundImage: `url('${this.state.item.BACKDROP}')`,
          }}
          className="rd-ct"
        ></div>
      </div>
    );
  }
}

export function FormatRuntime(time: number) {
  var minute: number = Math.floor(time % 60);
  var heure: number = Math.floor(time / 60);
  return `${heure > 0 ? heure + "h" : ""}${minute > 0 ? minute + "m" : ""}`;
}
