import { Link, useNavigate } from "react-router-dom";
import "./head.css";
import { KeyboardEvent, MouseEvent, MouseEventHandler, useState } from "react";
export const Head = (props: {}) => {
  const nav = useNavigate();
  const send = (e: string) => {
    console.log("nav");
    // nav(e);
    document.location.href = e;
  };
  if (document.location.href.includes("/player")) {
    return <div></div>;
  }
  return (
    <div className="hd-ctn">
      <div className="hd-left">
        <div className="">Netflix</div>
        <a className="hd-link" onClick={(_) => send("/")}>
          Browse
        </a>
        <a className="hd-link" onClick={(_) => send("/browse?action=movie")}>
          Movie
        </a>
        <a className="hd-link" onClick={(_) => send("/browse?action=tv")}>
          Tv
        </a>
        <a
          className="hd-link"
          onClick={(_) => send("/browse?action=watchlist")}
        >
          My List
        </a>
        <input
          onChange={(e: any) => {
            nav(`/search?q=${e.target.value}`);
          }}
          type="text"
          className="hd-search"
        />
      </div>
    </div>
  );
};
