import React from "react";
import ReactDOM from "react-dom/client";
import {
  Navigate,
  Route,
  BrowserRouter as Router,
  Routes,
} from "react-router-dom";
import { Render } from "./render/render";
import { Landing } from "./landing";
import { Browse } from "./browser/browse";
import { Head } from "./component/header/head";
import { SearchRender } from "./search/search";
import { PlayerRender } from "./player/player";

export const app_url = "";
const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);
root.render(
  <div>
    <Router>
      <Head />
      <div style={{ paddingTop: "var(--hd-height)" }}>
        <Routes>
          <Route path="/" element={<Landing />} />
          <Route path="/render/:Type/:id" element={<Render />} />
          <Route path="/browse" element={<Browse />} />
          <Route path="/search" element={<SearchRender />} />
          <Route path="/player" element={<PlayerRender />} />
          <Route
            path="/watchlist"
            element={<Navigate to={"/browse?action=watchlist"} />}
          />
        </Routes>
      </div>
    </Router>
  </div>
);
