import React, { useEffect } from "react";
import {
  NavigateFunction,
  Params,
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import { app_url } from "..";
import { MovieItem } from "../render/render";
import Hls from "hls.js";
import "./play.css";
//@ts-ignore
import valid from "./img/valid.svg";
//@ts-ignore
import back from "./img/back.svg";
//@ts-ignore
import forward from "./img/forward.svg";
//@ts-ignore
import play from "./img/play.svg";
//@ts-ignore
import pause from "./img/pause.svg";
//@ts-ignore
import volume from "./img/volume.svg";
//@ts-ignore
import code from "./img/code.svg";
//@ts-ignore
import fullscreen from "./img/fullscreen.svg";
//@ts-ignore
import fullscreenExit from "./img/fullscreen-exit.svg";
//@ts-ignore

export const PlayerRender = (props: {}) => {
  const params = useParams();
  const nav = useNavigate();
  const [data, setData] = React.useState<TranscodeDATA | null>(null);
  const [searchParams, setSearchParams] = useSearchParams();
  var transcodeUrl = searchParams.get("transcode");
  useEffect(() => {
    fetch(app_url + transcodeUrl, { credentials: "include" })
      .then((res) => res.json())
      .then((data) => {
        setData(data);
      });
  }, []);
  if (data === null) {
    return <div>Loading...</div>;
  }
  console.log(data);
  return <Player data={data} nav={nav} params={params} />;
};
function secondsToHms(seconds: number) {
  const h = Math.floor(seconds / 3600);
  const m = Math.floor((seconds % 3600) / 60);
  const s = Math.floor(seconds % 60);
  return `${h.toString().padStart(2, "0")}:${m.toString().padStart(2, "0")}:${s
    .toString()
    .padStart(2, "0")}`;
}
export interface PlayerProps {
  data: TranscodeDATA;
  params: Readonly<Params<string>>;
  nav: NavigateFunction;
}
const barHeight = 70;
const PlayBar: React.CSSProperties = {
  height: `${barHeight}px`,
  position: "relative",
  width: "100%",
  bottom: `${barHeight * 1.21}px`,
  zIndex: 2,
  transition: "ease-in-out opacity 0.5s",
  background:
    "linear-gradient(180deg, rgba(9,9,121,0.18820028011204482) 0%, rgba(0,0,0,0.7008053221288515) 31%, rgba(0,0,0,1) 100%)",
};
export interface QUALITY {
  Name: string;
  Resolution: number;
  VideoBitrate: number;
  AudioBitrate: number;
}
export interface TranscodeDATA {
  uuid: string;
  qualitys: QUALITY[];
  tracks: Track[];
  manifest: string;
  // item: MovieItem ;
}
export interface Track {
  Index: number;
  Name: string;
}
export interface PlayerState {
  CurrentQuality: QUALITY;
  CurrentTrack: Track;
}
interface SettingData {
  open: boolean;
  tracks: boolean;
  quality: boolean;
  subtitle: boolean;
}
const SYNC = {
  BAR: "bar",
  VIDEO: "video",
};
export class Player extends React.Component<PlayerProps> {
  container: React.RefObject<HTMLDivElement> = React.createRef();
  state: {
    settings: SettingData;
  };
  uuid: string;
  AvailableQuality: QUALITY[];
  static CURSOR_TIMEOUT = 3000;
  AvailableTracks: Track[];
  video: React.RefObject<HTMLVideoElement> = React.createRef();
  playBar: React.RefObject<HTMLDivElement> = React.createRef();
  playPauseBtn = React.createRef<HTMLImageElement>();
  hidetimeout: NodeJS.Timeout | null = null;
  timeline: React.RefObject<HTMLInputElement> = React.createRef();
  timeUpdate = React.createRef<HTMLDivElement>();
  fullscreen = React.createRef<HTMLImageElement>();
  hls: Hls = new Hls();
  settings = React.createRef<HTMLImageElement>();
  inited = false;
  constructor(props: PlayerProps) {
    document.title = `Player - ${props.data.uuid}`;
    console.log(props);
    super(props);
    this.uuid = props.data.uuid;
    this.AvailableQuality = props.data.qualitys;
    this.AvailableTracks = props.data.tracks;
    this.state = {
      settings: {
        open: false,
        subtitle: false,
        quality: false,
        tracks: false,
      },
    };
  }
  newHlsPlayer(setTimeStamp: number) {
    this.hls = new Hls();
    this.hls.config.xhrSetup = this.xhrSetup.bind(this);
    this.hls.config.maxBufferLength = 8;

    if (setTimeStamp !== 0) {
      this.hls.config.startPosition = setTimeStamp;
    }
    if (!this.inited) {
      this.inverseFullscreen();
      this.inited = true;
    }
    this.hls.loadSource(app_url + this.props.data.manifest);
    this.hls.attachMedia(this.video.current as HTMLVideoElement);
    this.hls.on(Hls.Events.ERROR, (e, b) => {
      console.log(e, b);
    });
  }
  componentDidMount() {
    document.body.style.backgroundColor = "black";
    if (!Hls.isSupported()) {
      console.log("hls not supported");
    }
    // this.newHlsPlayer(this.props.data.Watch.Current);
    this.newHlsPlayer(0);
    this.hidetimeout = setTimeout(() => {
      document.body.style.cursor = "none";
      this.hideTimeline();
    }, Player.CURSOR_TIMEOUT);
    document.addEventListener("keydown", this.KeyInputElement.bind(this));
    this.video.current?.addEventListener("ended", this.onEnded.bind(this));
  }
  onEnded() {
    console.log("ended");
    // if (!this.NextFile.Uuid) return;
    // document.location.href = `/play/${this.NextFile.ItemType}/${this.NextFile.Uuid}`;
  }
  KeyInputElement(e: KeyboardEvent) {
    if (!this.playBar.current || !this.video.current) return () => {};
    var videoTime = this.video.current.currentTime;
    if (e.key === "ArrowRight") {
      this.video.current.currentTime = videoTime + 10;
    }
    if (e.key === "ArrowLeft") {
      this.video.current.currentTime = videoTime - 10;
    }
    if (e.code === "Space") {
      this.inversePlay(false);
    }
  }
  xhrSetup(xhr: XMLHttpRequest, url: string) {
    xhr.withCredentials = true;
    if (url.includes("m3u8")) return;
    xhr.setRequestHeader("X-QUALITY", this.AvailableQuality[0].Name);
    xhr.setRequestHeader("X-TRACK", this.AvailableTracks[0].Index.toString());
    // if (this.subtitle.length > 0) {
    //   xhr.setRequestHeader("X-SUBTITLE", this.subtitle[0].Index.toString());
    // }
  }

  hideTimeline() {
    if (!this.playBar.current) return;
    this.playBar.current.style.opacity = "0";
  }
  showNextPoster() {
    console.log("show");
    this.setState({ nextPoster: true });
  }
  hideNextPoster() {
    console.log("hide");
    this.setState({ nextPoster: false });
  }
  setNewQuality(quality: QUALITY) {
    if (!this.video.current) return;
    var index = this.AvailableQuality.indexOf(quality);
    this.AvailableQuality.splice(index, 1);
    this.AvailableQuality.unshift(quality);
    this.setState({
      settings: {
        quality: false,
        tracks: false,
        open: false,
      },
    });
    const time = this.video.current.currentTime;
    console.log("destroy");
    this.hls.destroy();
    this.newHlsPlayer(time);
  }
  setNewTrack(track: Track) {
    if (!this.video.current) return;
    var index = this.AvailableTracks.indexOf(track);
    this.AvailableTracks.splice(index, 1);
    this.AvailableTracks.unshift(track);
    this.setState({
      settings: {
        quality: false,
        tracks: false,
        open: false,
      },
    });
    const time = this.video.current.currentTime;
    this.hls.destroy();
    this.newHlsPlayer(time);
  }
  // setNewSubtitle(subtitle: Subtitle) {
  //   if (!this.video.current) return;
  //   var index = this.subtitle.indexOf(subtitle);
  //   this.subtitle.splice(index, 1);
  //   this.subtitle.unshift(subtitle);
  //   this.setState({
  //     settings: {
  //       quality: false,
  //       tracks: false,
  //       open: false,
  //     },
  //   });
  //   this.video.current.load();
  // }
  componentWillUnmount(): void {
    document.removeEventListener("keydown", this.KeyInputElement);
    this.video.current?.removeEventListener("ended", this.onEnded);
  }
  resetHideTimeout() {
    if (this.hidetimeout === null || !this.playBar.current) return;
    clearTimeout(this.hidetimeout);
    document.body.style.cursor = "auto";
    this.hidetimeout = setTimeout(
      () => this.hideTimeline(),
      Player.CURSOR_TIMEOUT
    );
    this.playBar.current.style.opacity = "1";
  }
  videoLoad() {
    if (!this.timeline.current) throw new Error("wsh");
    this.timeline.current.max = (this.video.current?.duration as number)
      .toFixed(3)
      .toString() as string;
  }

  syncRange(type: string) {
    if (!this.timeline.current || !this.video.current)
      throw new Error("missing");
    type === SYNC.BAR
      ? (this.timeline.current.value = this.video.current.currentTime
          .toFixed(3)
          .toString() as string)
      : (this.video.current.currentTime = parseFloat(
          this.timeline.current.value
        ));
    this.setTime();
  }
  setTime() {
    if (!this.timeUpdate.current || !this.video.current)
      throw new Error("missing");
    this.timeUpdate.current.innerText = `${secondsToHms(
      this.video.current?.currentTime as number
    )}  /  ${secondsToHms(this.video.current?.duration as number)}`;
  }
  videoEnd() {
    if (!this.timeline.current || !this.video.current) {
      throw new Error("missing");
    }
    this.timeline.current.value = this.timeline.current.max;
  }
  async pauseHandler() {
    if (!this.playPauseBtn.current) return;
    this.playPauseBtn.current.src = play;
  }
  async playHandler() {
    if (!this.playPauseBtn.current) return;
    this.playPauseBtn.current.src = pause;
  }
  inversePlay(preventAnimation: boolean) {
    if (!this.video.current) return;
    this.video.current.paused
      ? this.video.current.play()
      : this.video.current.pause();
  }
  inverseFullscreen() {
    if (!this.container.current) return;
    if (!this.fullscreen.current) return;
    if (document.fullscreenElement) {
      document.exitFullscreen();
      this.fullscreen.current.src = fullscreen;
    } else {
      this.container.current.requestFullscreen().catch((e) => console.log(e));
      this.fullscreen.current.src = fullscreenExit;
    }
  }

  render() {
    return (
      <div onMouseMove={() => this.resetHideTimeout()} className="allcontainer">
        <div className="playcontainer" ref={this.container}>
          <video
            onTimeUpdate={() => this.syncRange(SYNC.BAR)}
            onLoadedData={() => this.videoLoad()}
            onPlay={() => this.playHandler()}
            onPause={() => this.pauseHandler()}
            onEnded={() => this.videoEnd()}
            onClick={() => this.inversePlay(false)}
            autoPlay={true}
            src={"/video.mp4"}
            className="videos"
            ref={this.video}
          ></video>
          <div ref={this.playBar} style={PlayBar}>
            <div className="content">
              <div className="timeline-container">
                <input
                  title="timeline"
                  step={0.01}
                  onChange={() => this.syncRange(SYNC.VIDEO)}
                  ref={this.timeline}
                  type="range"
                  className="timeline"
                />
              </div>
              <div className="line-icons">
                <div style={{ display: "flex" }}>
                  <img
                    title="PLay button"
                    ref={this.playPauseBtn}
                    onClick={() => this.inversePlay(true)}
                    className="btn-ic"
                    src={play}
                  />
                  <div
                    onMouseEnter={(e) => this.showNextPoster()}
                    onMouseLeave={() => this.hideNextPoster()}
                  >
                    <img title="forwardbtn" className="btn-ic" src={forward} />
                    {/* {this.state.nextPoster ? (
                      <PlayerPoster
                        NoNext={this.props.data.NoNext}
                        onClick={() => `/play/${this.NextFile.Uuid}`}
                        ImgUrl={this.NextFile.ImgUrl}
                        Name={this.NextFile.Name}
                        Uuid={this.NextFile.Uuid}
                      />
                    ) : (
                      <></>
                    )} */}
                  </div>
                </div>
                <div className="mid-left">
                  <img title="timestamp" className="btn-ic" src={volume} />
                  <div ref={this.timeUpdate} className="timesptamp">
                    00:00/20:00
                  </div>
                </div>
                <div className="right-icons">
                  {
                    <MiniModal
                      settingData={this.state.settings}
                      settings={this.settings}
                      player={this}
                      updateData={this.setState.bind(this)}
                    />
                  }
                  <div>
                    <span className="qual-label">
                      &nbsp;{this.AvailableQuality[0].Name.slice(0, 7)}&nbsp;
                    </span>
                    <span className="lang-label">
                      &nbsp;{this.AvailableTracks[0].Name.slice(0, 3)}&nbsp;
                    </span>
                    <img
                      title="settings"
                      ref={this.settings}
                      onClick={() =>
                        this.setState({
                          settings: {
                            quality: false,
                            tracks: false,
                            open: !this.state.settings.open,
                          },
                        })
                      }
                      src={code}
                      className="btn-ic"
                      style={{ marginRight: "7px", marginBottom: "2px" }}
                    />
                  </div>

                  <img
                    title="fullscreen"
                    onClick={() => this.inverseFullscreen()}
                    ref={this.fullscreen}
                    className="btn-ic"
                    src={fullscreen}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

function MiniModal(props: {
  settings: React.RefObject<HTMLImageElement>;
  settingData: SettingData;
  player: Player;
  updateData: Function;
}) {
  if (!props.settingData.open) {
    return <></>;
  }
  if (props.settingData.quality) {
    return (
      <div className="settings">
        {props.player.AvailableQuality.map((e, i) => {
          return (
            <div
              onClick={() => props.player.setNewQuality(e)}
              className="sett-obj"
            >
              {i === 0 ? (
                <img
                  style={{
                    position: "relative",
                    bottom: "2.5px",
                    right: "5px",
                  }}
                  src={valid}
                />
              ) : (
                ""
              )}
              {e.Name}
            </div>
          );
        })}
      </div>
    );
  }
  // if (props.settingData.subtitle) {
  //   return (
  //     <div className="settings">
  //       {props.player.subtitle.map((e, i) => {
  //         return (
  //           <div
  //             onClick={() => props.player.setNewSubtitle(e)}
  //             className="sett-obj"
  //           >
  //             {i === 0 ? (
  //               <img
  //                 style={{
  //                   position: "relative",
  //                   bottom: "2.5px",
  //                   right: "5px",
  //                 }}
  //                 src={valid}
  //               />
  //             ) : (
  //               ""
  //             )}{" "}
  //             {e.Language}
  //           </div>
  //         );
  //       })}
  //     </div>
  //   );
  // }
  if (props.settingData.tracks) {
    return (
      <div className="settings">
        {props.player.AvailableTracks.map((e, i) => {
          return (
            <div
              onClick={() => props.player.setNewTrack(e)}
              className="sett-obj"
            >
              {i === 0 ? (
                <img
                  style={{
                    position: "relative",
                    bottom: "2.5px",
                    right: "5px",
                  }}
                  src={valid}
                />
              ) : (
                ""
              )}
              {e.Name}
            </div>
          );
        })}
      </div>
    );
  }
  return (
    <div className="settings">
      <div
        className="sett-obj"
        onClick={() =>
          props.updateData({
            settings: {
              open: true,
              quality: true,
              tracks: false,
              subtitle: false,
            },
          })
        }
      >
        Quality
      </div>
      <div
        className="sett-obj"
        onClick={() =>
          props.updateData({
            settings: {
              open: true,
              quality: false,
              tracks: true,
              subtitle: false,
            },
          })
        }
      >
        Tracks
      </div>
      <div
        className="sett-obj"
        onClick={() =>
          props.updateData({
            settings: {
              open: true,
              quality: false,
              tracks: false,
              subtitle: true,
            },
          })
        }
      >
        Subtitle
      </div>
    </div>
  );
}
