import _ from "lodash";
import { PosterRenderer, SKINNY_RENDER } from "../poster";
import "./cd.css";

export const ChunkDisplay = (props: {
  items: SKINNY_RENDER[];
  chunk_size: number;
}) => {
  return (
    <div>
      {_.chunk(props.items, props.chunk_size).map((item: SKINNY_RENDER[]) => (
        <div className="cd-row">
          {item.map((item) => (
            <PosterRenderer {...item} />
          ))}
        </div>
      ))}
    </div>
  );
};
