import React, { useEffect } from "react";
import { PosterRenderer, SKINNY_RENDER } from "../component/poster";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "./main.css";
import { LineName } from "../component/lineName";

export interface WATCHING_RENDER {
  ITEM: SKINNY_RENDER;
  CURRENT: number;
  TOTAL: number;
  EPISODE_ID: number;
  FILE_ID: number;
}
export interface Home {
  watching: WATCHING_RENDER[];
  watchlist: SKINNY_RENDER[];
}

export const Landing = (props: {}) => {
  const [Home, setHome] = React.useState<null | Home>(null);
  useEffect(() => {
    fetch("http://localhost:8080/home", { credentials: "include" })
      .then((response) => response.json())
      .then((data) => setHome(data));
  }, []);
  if (!Home) return <div>Loading...</div>;
  return (
    <div>
      {/* <LineName more="/" lineName="Watchlist" />
      <Swiper slidesPerView={7}>
        {Home.watchlist.map((item: SKINNY_RENDER) => (
          <SwiperSlide key={item.ID}>
            <PosterRenderer {...item} />
          </SwiperSlide>
        ))}
      </Swiper>
      <LineName more="/" lineName="Watching" />
      <Swiper slidesPerView={7}>
        {Home.watchlist.map((item: SKINNY_RENDER) => (
          <SwiperSlide key={item.ID}>
            <PosterRenderer {...item} />
          </SwiperSlide>
        ))}
      </Swiper> */}
    </div>
  );
};
